<template>
    <div class="m-dashboard m-dashboard-work">
        <el-input
            class="m-dashboard-work-search"
            placeholder="请输入内容"
            v-model="search"
            @change="searchPost"
        >
            <el-select
                class="u-select"
                v-model="searchType"
                slot="prepend"
                placeholder="请选择"
                @change="searchPost"
            >
                <el-option label="全部" value=""></el-option>
                <el-option
                    :label="item"
                    :value="key"
                    v-for="(item, key) in types"
                    :key="key"
                ></el-option>
            </el-select>
            <el-button
                slot="append"
                icon="el-icon-search"
                @click="searchPost"
            ></el-button>
        </el-input>

        <div class="m-dashboard-box">
            <ul class="m-dashboard-box-list" v-if="data.length">
                <li v-for="(item, i) in data" :key="i">
                    <i class="u-icon">
                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 12 16" width="12" height="16" aria-hidden="true" fill="#555" v-if="item.post_status == 'publish'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path fill-rule="evenodd" d="M4 9H3V8h1v1zm0-3H3v1h1V6zm0-2H3v1h1V4zm0-2H3v1h1V2zm8-1v12c0 .55-.45 1-1 1H6v2l-1.5-1.5L3 16v-2H1c-.55 0-1-.45-1-1V1c0-.55.45-1 1-1h10c.55 0 1 .45 1 1zm-1 10H1v2h2v-1h3v1h5v-2zm0-10H2v9h9V1z"/></svg>
                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 12 16" width="12" height="16" fill="#dbab09" v-else="" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path fill-rule="evenodd" d="M4 13H3v-1h1v1zm8-6v7c0 .55-.45 1-1 1H1c-.55 0-1-.45-1-1V7c0-.55.45-1 1-1h1V4c0-2.2 1.8-4 4-4s4 1.8 4 4v2h1c.55 0 1 .45 1 1zM3.8 6h4.41V4c0-1.22-.98-2.2-2.2-2.2-1.22 0-2.2.98-2.2 2.2v2H3.8zM11 7H2v7h9V7zM4 8H3v1h1V8zm0 2H3v1h1v-1z"/></svg>
                    </i>
                    <a
                        class="u-title"
                        target="_blank"
                        :href="postLink(item.post_type, item.ID)"
                        >{{ item.post_title || "无标题" }}</a
                    >
                    <div class="u-desc">
                        <span class="u-desc-subitem"
                            ><i class="el-icon-folder"></i> 分类 :
                            {{ item.post_type | typeFormat }}</span
                        >
                        <time class="u-desc-subitem"
                            ><i class="el-icon-finished"></i> 发布 :
                            {{ item.post_date | dateFormat }}</time
                        >
                        <time class="u-desc-subitem"
                            ><i class="el-icon-refresh"></i> 更新 :
                            {{ item.post_modified | dateFormat }}</time
                        >
                    </div>

                    <el-button-group class="u-action">
                        <el-button
                            type="primary"
                            size="mini"
                            icon="el-icon-edit"
                            title="编辑"
                            @click="edit(item.post_type, item.ID)"
                        ></el-button>
                        <el-button
                            v-if="item.post_status == 'publish'"
                            type="primary"
                            size="mini"
                            icon="el-icon-lock"
                            title="设为私有"
                            @click="draft(item.ID, i)"
                        ></el-button>
                        <el-button
                            v-else
                            type="primary"
                            size="mini"
                            icon="el-icon-check"
                            title="设为公开"
                            @click="publish(item.ID, i)"
                        ></el-button>
                        <el-button
                            type="primary"
                            size="mini"
                            icon="el-icon-delete"
                            title="删除"
                            @click="del(item.ID)"
                        ></el-button>
                    </el-button-group>
                </li>
            </ul>
            <el-alert
                v-else
                class="m-dashboard-box-null"
                title="没有找到相关条目"
                type="info"
                center
                show-icon
            >
            </el-alert>
            <el-pagination
                class="m-dashboard-box-pages"
                background
                :page-size="per"
                :hide-on-single-page="true"
                @current-change="changePage"
                :current-page.sync="page"
                layout="total, prev, pager, next, jumper"
                :total="total"
            >
            </el-pagination>
        </div>
    </div>
</template>

<script>
import { getWorks, delPost, hidePost, publishPost } from "../service/work";
import { editLink } from "@jx3box/jx3box-common/js/utils";
import {
    __v2,
    __Root,
    __Links,
    __postType,
} from "@jx3box/jx3box-common/js/jx3box";
import dateFormat from "../utils/dateFormat";

export default {
    name: "work",
    props: [],
    data: function() {
        return {
            data: [],
            total: 1,
            page: 1,
            per: 10,
            search: "",
            searchType: "",
            types: __postType,
        };
    },
    computed: {},
    methods: {
        loadPosts: function(i = 1) {
            getWorks({
                page: i,
                title: this.search,
                type: this.searchType,
                per: this.per,
            }).then((res) => {
                this.data = res.data.data.list;
                this.total = res.data.data.total;
            });
        },
        changePage: function(i = 1) {
            this.loadPosts(i);
        },
        searchPost: function(i = 1) {
            this.loadPosts(i);
        },
        edit: function(type, id) {
            location.href = __Links.dashboard.publish + "#/" + type + "/" + id;
        },
        del: function(id) {
            this.$alert("确定要删除吗？", "确认信息", {
                confirmButtonText: "确定",
                callback: (action) => {
                    if (action == "confirm") {
                        delPost(id)
                            .then(() => {
                                this.$message({
                                    type: "success",
                                    message: `删除成功`,
                                });
                                location.reload();
                            })
                    }
                },
            });
        },
        draft: function(id, i) {
            hidePost(id)
                .then((res) => {
                    this.$message({
                        type: "success",
                        message: `操作成功`,
                    });
                    this.data[i].post_status = "draft";
                })
        },
        publish: function(id, i) {
            publishPost(id)
                .then((res) => {
                    this.$message({
                        type: "success",
                        message: `操作成功`,
                    });
                    this.data[i].post_status = "publish";
                })
        },
        postLink: function(type, id) {
            return __Root + type + "/?pid=" + id;
        },
    },
    filters: {
        dateFormat: function(val) {
            return dateFormat(new Date(val));
        },
        typeFormat: function(val) {
            return __postType[val];
        },
    },
    mounted: function() {
        let route_type = this.$route.params.type;
        if (route_type) {
            this.searchType = route_type;
        }
        this.loadPosts();
    },
};
</script>

<style scoped lang="less">
@import "../assets/css/work.less";
</style>
